//lookMission.vue
<template>
	<div class="lookMission">
		<!-- 内容-->
		<div class="container">
			<div class="title-box">
				<img slot="icon" src="../../static/icon/left.svg" width="24" height="24" data="0" v-on:click="toPage">	
				<div class="m-office">
					查看任务
				</div>
				<div class="m-icon">
					
				</div>	
			</div>
			<div class="logArea">
				<div class="flowArea">
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务名称
						</div>
						<div  class="flowStatus2">
							{{ missionName }}
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务描述
						</div>
						<div  class="flowStatus2">
							{{ missionDescribe }}
						</div>
					</div>
					<div class="flowStatus" v-show="haveTime">
						<div  class="flowStatus1">
							开始时间
						</div>
						<div  class="flowStatus2">
							{{ startTime }}
						</div>
					</div>
					<div class="flowStatus" v-show="haveTime">
						<div  class="flowStatus1">
							结束时间
						</div>
						<div  class="flowStatus2">
							{{ endTime }}
						</div>
					</div>
				</div>
				<div class="flowArea">
					<h3></h3>
					<div class="flowMould">
						<p>执行人</p>
						<div class="flowChart examine">
						</div>
						<p>必须的执行人</p>
						<div class="flowChart mustExamine">
						</div>
					</div>
					<div class="flowMould">
						<p>抄送人</p>
						<div class="flowChart copyTo">
						</div>
						<p>必须的抄送人</p>
						<div class="flowChart mustCopyTo">
						</div>
					</div>
				</div>
				<div class="flowArea">
					<div class="flowStatus">
						<div  class="flowStatus1">
							进度值
						</div>
						<div  class="flowStatus2">
							<input type="text" class="realityValue"/>
							<input type="hidden" class="headId" />
						</div>
					</div>

				</div>
			</div>

		</div>
		<div class="flowBottom" v-on:click="submit">
			提交
		</div>
	</div>
</template>

<!-- script -->
<script>
import { Toast } from 'mint-ui';
export default {
	data() {
		return {
			items:[],
			missionName:'',
			missionDescribe:'',
			startTime:'',
			endTime:'',
			haveTime:true,
			because:''
		}
	},
	mounted () {
		var that = this;
		var info = {
			missionId:this.$leaveType.missionId,
			
		}
		that.$index.ajax(that,'/phMission/selectMissionById.ph',info,function(data){
			that.missionName = data.rows[0].missionName;
			that.missionDescribe = data.rows[0].missionDescribe;
			that.startTime = data.rows[0].startTimeStr;
			that.endTime = data.rows[0].expectEndTimeStr;
			var value = data.rows[0].realityValue;
			$('.lookMission .flowArea .realityValue').val(value);
			if(value == 100){
				$('.lookMission .flowArea .realityValue').attr('readonly','readonly');
			}
			$('.lookMission .flowArea .headId').val(data.rows[0].headId);

		});
		that.$index.ajax(that,'/phMission/selectMissionUserName.ph',info,function(data){
			if(data.rows.length != 0){
				var str = '';
				for(var i = 0;i < data.rows.length ;i++){
					str +='<span class="flowPeople">'+data.rows[i].userName+'</span>' ;
				}
				$('.lookMission .flowArea .copyTo').html(str);
				$('.lookMission .flowArea .copyTo img:last').remove();
			}
		});
		that.$index.ajax(that,'/phMission/selectMissionHeadUser.ph',info,function(data){
			if(data.rows.length != 0){
				var str = '';
				for(var i = 0;i < data.rows.length ;i++){
					str +='<span class="flowPeople">'+data.rows[i].userName+'</span>' ;
				}
				$('.lookMission .flowArea .examine').html(str);
				$('.lookMission .flowArea .examine img:last').remove();
			}
		});

	},
	methods: {
		toPage:function(event){
			var el = event.currentTarget;
			var a = $(el).attr('data');
			if (a == 0) {
				this.$router.push({path:'/mission'});
			}
		},
		submit:function(event){
			var that = this;
			var realityValue = $('.lookMission .flowArea .realityValue').val();
			var headId = $('.lookMission .flowArea .headId').val();
			var reg = new RegExp("^(\\d|[1-9]\\d|100)$");
			if (realityValue == '' || realityValue == null) {
				Toast({
					 message: '请填写进度值',
					 duration: 1000
				});
			}else if(!reg.test(realityValue)){
				Toast({
					 message: '请输入1-100的整数！',
					 duration: 1000
				});
			}else{
				var info = {
					headId:headId,
					realityValue:realityValue
				}
				that.$index.ajax(that,'/phMission/updateMissionState.ph',info,function(data){
					Toast({
						message: '提交成功！',
						position: 'center',
						duration: 2000
					});
					that.$router.push({path:'/mission'});
				});
			}

		},

	}
}
</script>




