//launchMission.vue
<template>
	<div class="launchMission">
		<div class="container">
			<div class="title-box">
				<img slot="icon" src="../../static/icon/left.svg" wigth="24" height="24" data="0" v-on:click="toPage">

				<div class="m-office">
					我发起的任务
				</div>
				<div class="m-icon">
					<div class="office-icon">
						<img slot="icon" src="../../static/icon/refresh.svg" width="24" height="24" v-on:click="refresh">
					</div>
				</div>
			</div>
			<div class="logArea">
				<div class="flowArea" v-for="item of items">
					<h3>{{ item.missionName }}</h3>
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务名称
						</div>
						<div  class="flowStatus2">
							{{ item.missionName }}
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务描述
						</div>
						<div  class="flowStatus2">
							{{ item.missionDescribe }}
						</div>
					</div>
					<div class="button" v-bind:id="item.missionId":headId="item.headId"
					 data="1" v-on:click="toPage">查看</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	data() {
		return {
			items:[]
		}
	},
	mounted () {
		var info = {
			userId : this.$user.userId,
			userName : this.$user.userName
		}
		var that = this;
		that.$index.ajax(that,'/phMission/selectMyLaunchMission.ph',null,function(data){
			that.items = data.rows;
		});
	},
	methods: {
		toPage:function(event){
			var el = event.currentTarget;
			var a = $(el).attr('data');
			if (a == 0) {
				this.$router.push({path:'/'});
			}else if (a == 1) {
				var info = {
					missionId:$(el).attr('id'),
					headId:$(el).attr('headId')
				}
				$.extend(this.$leaveType,info);
				this.$router.push({path:'/lookLaunchMission'});
			}
		},
		refresh:function(){
			window.location.reload()
		}
	}
}

</script>


