//lookMission.vue
<template>
	<div class="lookLaunchMission">
		<!-- 内容-->
		<div class="container">
			<div class="title-box">
				<img slot="icon" src="../../static/icon/left.svg" width="24" height="24" data="0" v-on:click="toPage">	
				<div class="m-office">
					查看任务
				</div>
				<div class="m-icon">
					
				</div>	
			</div>
			<div class="logArea">
				<div class="flowArea">
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务名称
						</div>
						<div  class="flowStatus2">
							{{ missionName }}
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务描述
						</div>
						<div  class="flowStatus2">
							{{ missionDescribe }}
						</div>
					</div>
					<div class="flowStatus" v-show="haveTime">
						<div  class="flowStatus1">
							开始时间
						</div>
						<div  class="flowStatus2">
							{{ startTime }}
						</div>
					</div>
					<div class="flowStatus" v-show="haveTime">
						<div  class="flowStatus1">
							结束时间
						</div>
						<div  class="flowStatus2">
							{{ endTime }}
						</div>
					</div>
				</div>
				<div class="flowArea">
					<h3></h3>
					<div class="flowMould">
						<p>执行人</p>
						<div class="flowChart examine">
						</div>
						<p>必须的执行人</p>
						<div class="flowChart mustExamine">
						</div>
					</div>
					<div class="flowMould">
						<p>抄送人</p>
						<div class="flowChart copyTo">
						</div>
						<p>必须的抄送人</p>
						<div class="flowChart mustCopyTo">
						</div>
					</div>
				</div>
				<div class="flowArea" v-for="item of items">
					<div class="flowStatus" style="border-top:none;">
						<div  class="flowStatus1">
							执行人
						</div>
						<div  class="flowStatus2">
							{{ item.headUserName }}
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							实际完成时间
						</div>
						<div  class="flowStatus2">
							{{ item.realityEndTimeStr }}
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							任务状态
						</div>
						<div  class="flowStatus2" v-if="item.missionState == 1">
							处理中
						</div>
						<div  class="flowStatus2" v-if="item.missionState == 2">
							审批中
						</div>
						<div  class="flowStatus2" v-if="item.missionState == 3">
							驳回
						</div>
						<div  class="flowStatus2" v-if="item.missionState == 4">
							已完成
						</div>
					</div>
					<div class="flowStatus">
						<div  class="flowStatus1">
							执行进度
						</div>
						<div  class="flowStatus2">
							{{ item.realityValue }} %
						</div>
					</div>
					<div class="button" data="1" v-on:click="examine" v-bind:id="item.headId" :missionState="item.missionState" :headUserId="item.headUserId" :launchUserId="item.launchUserId" :launchUserName="item.launchUserName" :headUserName="item.headUserName">
						通过
					</div>
					<div class="button" data="2" v-on:click="examine" v-bind:id="item.headId" :missionState="item.missionState" :headUserId="item.headUserId" :launchUserId="item.launchUserId" :launchUserName="item.launchUserName" :headUserName="item.headUserName">
						驳回
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<!-- script -->
<script>
import { Toast } from 'mint-ui';
export default {
	data() {
		return {
			items:[],
			missionName:'',
			missionDescribe:'',
			startTime:'',
			endTime:'',
			haveTime:true,
			because:''
		}
	},
	mounted () {
		var that = this;
		var info = {
			missionId:this.$leaveType.missionId,
			
		}
		that.$index.ajax(that,'/phMission/selectMissionById.ph',info,function(data){
			that.missionName = data.rows[0].missionName;
			that.missionDescribe = data.rows[0].missionDescribe;
			that.startTime = data.rows[0].startTimeStr;
			that.endTime = data.rows[0].expectEndTimeStr;
			that.items = data.rows;

		});
		that.$index.ajax(that,'/phMission/selectMissionUserName.ph',info,function(data){
			if(data.rows.length != 0){
				var str = '';
				for(var i = 0;i < data.rows.length ;i++){
					str +='<span class="flowPeople">'+data.rows[i].userName+'</span>' ;
				}
				$('.lookLaunchMission .flowArea .copyTo').html(str);
				$('.lookLaunchMission .flowArea .copyTo img:last').remove();
			}
		});
		that.$index.ajax(that,'/phMission/selectMissionHeadUser.ph',info,function(data){
			if(data.rows.length != 0){
				var str = '';
				for(var i = 0;i < data.rows.length ;i++){
					str +='<span class="flowPeople">'+data.rows[i].userName+'</span>' ;
				}
				$('.lookLaunchMission .flowArea .examine').html(str);
				$('.lookLaunchMission .flowArea .examine img:last').remove();
			}
		});
	},
	methods: {
		toPage:function(event){
			var el = event.currentTarget;
			var a = $(el).attr('data');
			if (a == 0) {
				this.$router.push({path:'/launchMission'});
			}
		},
		examine:function(event){
			var that = this;
			var el = event.currentTarget;
			var a = $(el).attr('data');
			var missionState = $(el).attr('missionState');
			var headUserId = $(el).attr('headUserId');
			var headUserName = $(el).attr('headUserName');
			var launchUserId = $(el).attr('launchUserId');
			var launchUserName = $(el).attr('launchUserName');
			if (a == 1) {
				if(missionState != '2'){
				Toast({
					 message: '此任务状态不在审批中，无法审批！',
					 duration: 1000
						});
					return;
				}
				var info = {
					headId:$(el).attr('id'),
					missionState:'4',
					headUserId:headUserId,
					headUserName:headUserName,
					launchUserId:launchUserId,
					launchUserName:launchUserName
				}
				that.$index.ajax(that,'/phMission/updateMissionState.ph',info,function(data){
					Toast({
						 message: '审批成功！',
						 duration: 1000
					});
					that.$router.push({path:'/launchMission'});
				});
			}else if(a == 2) {
				if(missionState != '2'){
					 Toast({
					 message: '此任务状态不在审批中，无法审批！',
					 duration: 1000
						});
					return;
				}
				var info = {
					headId:$(el).attr('id'),
					missionState:'3',
					headUserId:headUserId,
					headUserName:headUserName,
					launchUserId:launchUserId,
					launchUserName:launchUserName
				}

				that.$index.ajax(that,'/phMission/updateMissionState.ph',info,function(data){
					Toast({
						 message: '驳回成功！',
						 duration: 1000
					});
					that.$router.push({path:'/launchMission'});
				});
			}
			
		}
	}
}
</script>




