//addMission.vue
<template>
	<div class="addMission">
		<div class="container">
			<div class="title-box">
				<img slot="icon" src="../../static/icon/left.svg" width="24" height="24" data="0" v-on:click="toPage">
				<div class="m-office">
					任务
				</div>
				<div class="m-icon">
				</div>
			</div>
			<div class="logArea">
				<div class="flowArea flow">
				</div>
				<div class="flowArea">
					<h3>任务</h3>
					<div class="flowMould">
						<p>执行人</p>
						<div class="flowChart examine">
							<span class="flowPeople addPeople" v-on:click="addPeople" data="0">添加</span>
						</div>
					</div>
					<div class="flowMould">
						<p>抄送人</p>
						<div class="flowChart copyTo">
							<span class="flowPeople addPeople" v-on:click="addPeople" data="1">添加</span>
						</div>
					</div>
				</div>
			<div class="btnArea">
				<mt-button type="primary" size="large" 
				v-on:click="insertMission">发起任务</mt-button>
			</div>
			</div>
		</div>
	<div class="container chooseBox">
		<div class="title-box">
			<img slot="icon" src="../../static/icon/left.svg" width="24" height="24" data="0" v-on:click="back">
			<div class="m-office">
				选择人员
			</div>
			<div class="m-icon">
			</div>
		</div>
		<div class="chooseArea">
			<p> <input type="text" name="userName" placeholder="搜索" /><button v-on:click="queryUser">查询</button></p>
			<div class="choosePeople">
				<h2><span v-on:click="backChooseDet">{{ showDet }}</span></h2>
				<div class="chooseDet" v-for="det of dets">
					<div class="det" v-on:click="chooseUser" v-bind:id="det.id">
						{{ det.text }}
					</div>
				</div>
				<div class="chooseUser" v-for="user of users">
					<div class="user" v-on:click="completeChoose" v-bind:id="user.userId">
						{{ user.userName }}
					</div>
				</div>
				<div class="noResult">
					并未搜索到结果
				</div>
			</div>
		</div>
	</div>
	<mt-datetime-picker
		ref="beginPicker"
		type="datetime"
		v-model="beginDate"
		v-on:confirm="selectBeginDate">
	</mt-datetime-picker>
	<mt-datetime-picker
		ref="endPicker"
		type="datetime"
		v-model="endDate"
		v-on:confirm="selectEndDate">
	</mt-datetime-picker>
	</div>
</template>
 
<script>
import arrow from '../../static/icon/arrow.svg';
import { Toast } from 'mint-ui';
export default {
	data() {
		return {
			missionName:'',
			beginDate:new Date(),
			showBeginDate:new Date().Format('yyyy-MM-dd hh:mm'),
			endDate:new Date(),
			showEndDate:new Date().Format('yyyy-MM-dd hh:mm'),
			showDet:'',
			dets:[],
			users:[],
			flag:''
		}	
	},
	created () {
		// 组件创建完后执行
	},
	mounted () {
		//渲染完以后执行，生命周期内只执行一次，初始化数据
		var that = this;
		var flowStr = '';
		flowStr += '<div class="flowStatus">'
					+  '<div  class="flowStatus1">任务名称</div>'
					+  '<div  class="flowStatus2">'
					+  '<input type="text" class="missionName"/></div>'
					+  '</div>'
					+  '<div class="flowStatus">'
					+  '<div  class="flowStatus1">任务描述</div>'
					+  '<div  class="flowStatus2">'
					+  '<input type="text" class="missionDescribe"/></div>'
					+  '</div>'
					+  '<div class="flowStatus">'
					+  '<div  class="flowStatus1">开始时间</div>'
					+  '<div  class="flowStatus2 startTime"></div>'	
					+  '</div>'	
					+  '<div class="flowStatus">'
					+  '<div  class="flowStatus1">结束时间</div>'
					+  '<div  class="flowStatus2 endTime"></div>'	
					+  '</div>';
		$('.addMission .flow').append(flowStr);
		$('.addMission .flow .startTime').click(function(){
			that.$refs.beginPicker.open();
		});
		$('.addMission .flow .endTime').click(function(){
			that.$refs.endPicker.open();
		});
	},
	updated () {
		//数据更新重新渲染后会执行
	},
	methods: {
		selectBeginDate:function(value){
			//this.showBeginDate = value.Format('yyyy-MM-dd hh:mm');
			$('.addMission .flow .startTime').text(value.Format('yyyy-MM-dd hh:mm'));	
		},
		selectEndDate:function(value){
			//this.showEndDate = value.Format('yyyy-MM-dd hh:mm');
			$('.addMission .flow .endTime').text(value.Format('yyyy-MM-dd hh:mm'));
		},
		toPage:function(event){
			var el = event.currentTarget;
			var a = $(el).attr('data');
			var typeId = this.$leaveType.typeId;
			if (a == 0) {
				this.$router.push({path:'/mission'});
			}
		},
		addPeople:function(event){
			var that = this;
			var el = event.currentTarget;
			var a = $(el).attr('data');
			if (a == 0) {
				that.flag = 0;
			}else{
				that.flag = 1;
			}
			$('.addMission .container').eq(0).hide();
			that.$index.ajax(this,'/phDepartment/getFramework.ph',null,function(data){
				that.dets = data[0].children[0].children;
			});
			$('.addMission .chooseBox').show();
			$('.addMission .chooseBox .chooseDet').show();
			$('.addMission .chooseBox h2').hide();
			$('.addMission .chooseBox .chooseUser').hide();
		},
		chooseUser:function(event){
			var that = this;
			var el = event.currentTarget;
			var a = $(el).attr('id');
			var info = {
				departmentUrl:a,
				inviteFlag:'1'
			};
			that.$index.ajax(this,'/phUserInfo/selectUserInfo.ph',info,function(data){
				that.users = data.rows;
				$('.addMission .chooseBox .chooseDet').hide();
				that.showDet = $.trim($(el).text());
				$('.addMission .chooseBox h2').show();
				$('.addMission .chooseBox .chooseUser').show();
			});
		},
		completeChoose:function(event){
			var that = this;
			var el = event.currentTarget;
			var userId = $(el).attr('id');
			var userName = $.trim($(el).text());
			if(that.flag == 0){
				var str = '<span class="flowPeople" id="'+userId+'">'+userName+'</span>';
				$('.addMission .examine .addPeople').before(str);
				$('.addMission .chooseBox').hide();
				$('.addMission .container').eq(0).show();				
				$('.addMission .flowArea .examine .flowPeople[data!=0]').unbind('click');
				$('.addMission .flowArea .examine .flowPeople[data!=0]').click(function(){
					var jqthat = $(this);
					$(jqthat).remove();
				});
			}else{
				var str = '<span class="flowPeople" id="'+userId+'">'+userName+'</span>';
				$('.addMission .copyTo .addPeople').before(str);
				$('.addMission .chooseBox').hide();
				$('.addMission .container').eq(0).show();				
				$('.addMission .flowArea .copyTo .flowPeople[data!=1]').unbind('click');
				$('.addMission .flowArea .copyTo .flowPeople[data!=1]').click(function(){
					var jqthat = $(this);
					$(jqthat).remove();
				});
			}
			
		},
		backChooseDet:function(event){
			var el = event.currentTarget;
			$(el).parent().hide();
			$('.addMission .chooseBox .chooseUser').hide();
			$('.addMission .chooseBox .chooseDet').show();
		},
		queryUser:function(event){
			var userName = $('.addMission .chooseArea input[name=userName]').val();
			alert(userName);
			var that = this;
			var info = {
				userName:userName,
				inviteFlag:'1'
			};
			that.$index.ajax(this,'/phUserInfo/selectUserInfo.ph',info,function(data){
				that.users = data.rows;
				if (data.rows.length <= 0) {
					$('.addMission .chooseBox .noResult').show();
				}
				$('.addMission .chooseBox .title-box img').attr('data','back');
				$('.addMission .chooseBox .chooseDet').hide();
				$('.addMission .chooseBox .chooseUser').show();
			});
		},
		insertMission:function(){
			var that = this;
			var info;
			var missionName = $('.addMission .flow .missionName').val();
			var startTimeStr = $.trim($('.addMission .flow .startTime').text());
			var endTimeStr = $.trim($('.addMission .flow .endTime').text());
			var missionDescribe = $('.addMission .flow .missionDescribe').val();
			var userIds = '';
			var headUserIds = '';
			$('.addMission .copyTo .flowPeople').each(function(index, el) {
				var id = $(this).attr("id");
				if(typeof(id)!=='undefined'){
                	userIds = userIds + "" + id + ",";
            	}
			});
			$('.addMission .examine .flowPeople').each(function(index, el) {
				var id = $(this).attr("id");
				if(typeof(id)!=='undefined'){
                	headUserIds = headUserIds + "" + id + ",";
            	}
			});
			if (missionName == '' || missionName == null) {
				Toast({
					 message: '请填写任务名称',
					 duration: 1000
				});
			}else if (startTimeStr == '' || startTimeStr == null) {
				Toast({
					 message: '请选择开始时间',
					 duration: 1000
				});
			}else if (endTimeStr == '' || endTimeStr == null) {
				Toast({
					 message: '请选择结束时间',
					 duration: 1000
				});
			}else {
				info = {
					missionName:missionName,
					startTimeStr:startTimeStr,
					expectEndTimeStr:endTimeStr,
					userIds:userIds,
					headUserIds:headUserIds,
					missionDescribe:missionDescribe
				}
				that.$index.ajax(that,'/phMission/addMission.ph',info,function(data){
					Toast({
					  message: "发起任务成功",
					  duration: 3000
					});
					that.$router.push({path:'/mission'});
				});
			}
		},
		back:function(event){
			var el = event.currentTarget;
			var a = $(el).attr('data');
			if(a == 0){
				$('.addMission .container').eq(1).hide();
				$('.addMission .container').eq(0).show();
				$('.addMission .chooseBox h2').hide();
				$('.addMission .chooseBox .chooseDet').hide();
				$('.addMission .chooseBox .chooseUser').show();
			}else if (a == 'back') {
				$(el).attr('data',0);
				$('.addMission .chooseBox .noResult').hide();
				$('.addMission .chooseBox .chooseDet').show();
				$('.addMission .chooseBox .chooseUser').hide();
			}
		}

	}

}
</script>


